async function submitAuthKey() {
  const authKey = document.getElementById("authKeyInput").value.trim();
  const email = document.getElementById("authEmailInput").value.trim();
  
  if (!email) {
    showAuthStatus("이메일 주소를 입력해주세요.", "error");
    return;
  }
  
  if (!authKey) {
    showAuthStatus("인증키를 입력해주세요.", "error");
    return;
  }
  
  // Validate auth key format
  if (authKey.length < 8) {
    showAuthStatus("인증키는 최소 8자 이상이어야 합니다.", "error");
    return;
  }
  
  // Test authentication with backend
  showAuthStatus("인증을 확인하고 있습니다...", "info");
  
  // Store temporarily for validation
  localStorage.setItem("excel_addon_auth_key", authKey);
  localStorage.setItem("excel_addon_auth_email", email);
  
  // Test with a simple API call that requires auth
  try {
    const response = await fetch(API_PROXY_URL, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        command: "테스트",
        sheetContext: { 
          activeRange: { address: "A1" },
          lastRow: 1,
          lastColumn: 1,
          headers: []
        },
        model: "gpt-4.1-2025-04-14",  // Force premium model to trigger auth check
        authKey: authKey,
        authEmail: email
      })
    });
    
    // Check response status first
    if (response.status === 403) {
      // Definitely invalid auth key
      showAuthStatus("유효하지 않은 인증키입니다.", "error");
      localStorage.removeItem("excel_addon_auth_key");
      localStorage.removeItem("excel_addon_auth_email");
      return;
    }
    
    const data = await response.json();
    console.log('Auth validation response:', { status: response.status, data });
    
    // Check for any error in response or debug info
    if (!data.success || data.error || data.debug) {
      // Invalid auth key or other error
      const errorMessage = data.error || (data.debug ? "유효하지 않은 인증키입니다." : "인증 오류가 발생했습니다.");
      showAuthStatus(errorMessage, "error");
      localStorage.removeItem("excel_addon_auth_key");
      localStorage.removeItem("excel_addon_auth_email");
    } else {
      // Valid auth key - command was processed successfully
      showAuthStatus("인증이 완료되었습니다.", "success");
      
      // Update auth button text
      updateAuthButton();
      
      // Enable GPT-4.1 model
      const modelSelect = document.getElementById("modelSelect");
      modelSelect.value = "gpt-4.1-2025-04-14";
      handleModelChange();
      
      setTimeout(() => {
        hideAuthModal();
      }, 2000);
    }
  } catch (error) {
    console.error("Auth validation error:", error);
    showAuthStatus("인증 확인 중 오류가 발생했습니다.", "error");
    localStorage.removeItem("excel_addon_auth_key");
    localStorage.removeItem("excel_addon_auth_email");
  }
}

