// Format cell borders
async function executeBorderFormat(context, params) {
  console.log('executeBorderFormat called with params:', params);
  const worksheet = context.workbook.worksheets.getActiveWorksheet();
  
  try {
    let targetRanges = [];
    
    // Determine the range based on params
    if (!params.range) {
      // No range specified - handle multiple selections
      try {
        const selectedRanges = context.workbook.getSelectedRanges();
        selectedRanges.load(["areas", "areaCount"]);
        await context.sync();
        
        if (selectedRanges.areaCount > 1) {
          // Multiple non-contiguous ranges selected
          for (let i = 0; i < selectedRanges.areas.items.length; i++) {
            targetRanges.push(selectedRanges.areas.items[i]);
          }
        } else {
          // Single contiguous range
          targetRanges.push(context.workbook.getSelectedRange());
        }
      } catch (error) {
        console.log('Error getting selected ranges for border format/removal:', error);
        return { success: false, error: '선택한 범위를 처리할 수 없습니다. 연속된 범위를 선택해주세요.' };
      }
    } else if (params.range === 'all' || params.range === '전체') {
      // Entire sheet
      const usedRange = worksheet.getUsedRangeOrNullObject();
      usedRange.load("isNullObject");
      await context.sync();
      
      if (usedRange.isNullObject) {
        return { success: false, error: '시트에 데이터가 없습니다.' };
      } else {
        targetRanges.push(usedRange);
      }
    } else {
      // Specific range provided
      targetRanges.push(worksheet.getRange(params.range));
    }
    
    // Load all ranges
    for (const range of targetRanges) {
      range.load(["address", "rowCount", "columnCount"]);
    }
    await context.sync();
    
    console.log('Target ranges for border format:', targetRanges.length);
    for (let i = 0; i < targetRanges.length; i++) {
      console.log(`Range ${i + 1}:`, {
        address: targetRanges[i].address,
        rowCount: targetRanges[i].rowCount,
        columnCount: targetRanges[i].columnCount
      });
    }
    
    // Determine which borders to format
    const borderType = params.borderType || 'all';
    const color = params.color || '#000000';
    const style = params.style || 'Continuous';
    
    // Map style names to Excel constants
    const styleMap = {
      'continuous': 'Continuous',
      'dash': 'Dash',
      'dashdot': 'DashDot',
      'double': 'Double',
      'dot': 'Dot',
      'dashdotdot': 'DashDotDot',
      'slantdashdot': 'SlantDashDot'
    };
    
    const excelStyle = styleMap[style.toLowerCase()] || 'Continuous';
    
    // Apply border formatting to all selected ranges
    for (const targetRange of targetRanges) {
      if (borderType === 'all') {
        // Format all borders
        const borderItems = ['EdgeTop', 'EdgeBottom', 'EdgeLeft', 'EdgeRight', 'InsideVertical', 'InsideHorizontal'];
        
        for (const borderItem of borderItems) {
          try {
            const border = targetRange.format.borders.getItem(borderItem);
            border.style = excelStyle;
            border.color = color;
            border.weight = 'Thin';
          } catch (e) {
            // Some border items might not be applicable (e.g., InsideVertical for single cell)
            console.log(`Skipping ${borderItem}:`, e.message);
          }
        }
      } else if (borderType === 'inside') {
        // Format only inside borders
        try {
          const insideV = targetRange.format.borders.getItem('InsideVertical');
          insideV.style = excelStyle;
          insideV.color = color;
          insideV.weight = 'Thin';
        } catch (e) {
          console.log('No inside vertical borders');
        }
        
        try {
          const insideH = targetRange.format.borders.getItem('InsideHorizontal');
          insideH.style = excelStyle;
          insideH.color = color;
          insideH.weight = 'Thin';
        } catch (e) {
          console.log('No inside horizontal borders');
        }
      } else {
        // Format specific border
        const borderMap = {
          'top': 'EdgeTop',
          'bottom': 'EdgeBottom',
          'left': 'EdgeLeft',
          'right': 'EdgeRight'
        };
        
        const borderName = borderMap[borderType.toLowerCase()];
        if (borderName) {
          const border = targetRange.format.borders.getItem(borderName);
          border.style = excelStyle;
          border.color = color;
          border.weight = 'Thin';
        }
      }
    }
    
    await context.sync();
    
    return {
      success: true,
      message: '테두리 서식이 적용되었습니다.'
    };
    
  } catch (error) {
    console.error('Error in executeBorderFormat:', error);
    return {
      success: false,
      error: `테두리 서식 적용 중 오류 발생: ${error.message}`
    };
  }
}

// Additional operations (compress, filter, insert, delete, formula, retry_translation)
// These would need to be implemented based on Excel JavaScript API capabilities

async function executeCompress(context, params) {
  console.log('executeCompress called with params:', params);
  const worksheet = context.workbook.worksheets.getActiveWorksheet();
  
  try {
    if (!params.range) {
      return { success: false, error: '범위를 지정해주세요.' };
    }
    
    // Get the specified range
    const checkRange = worksheet.getRange(params.range);
    checkRange.load(["rowIndex", "rowCount", "columnIndex", "values", "address"]);
    await context.sync();
    
    console.log('Compress range:', {
      address: checkRange.address,
      rowIndex: checkRange.rowIndex,
      rowCount: checkRange.rowCount,
      columnIndex: checkRange.columnIndex
    });
    
    // Find empty rows in the specified column
    const emptyRows = [];
    const values = checkRange.values;
    
    // Check each row in the range
    for (let i = 0; i < values.length; i++) {
      const cellValue = values[i][0]; // First column of the range
      if (cellValue === null || cellValue === undefined || cellValue === '' || 
          (typeof cellValue === 'string' && cellValue.trim() === '')) {
        // This row is empty in the specified column
        // Store the actual row index (rowIndex + i)
        emptyRows.push(checkRange.rowIndex + i);
      }
    }
    
    console.log('Found empty rows:', emptyRows.length, 'rows at indices:', emptyRows);
    
    if (emptyRows.length === 0) {
      return {
        success: true,
        message: '제거할 빈 행이 없습니다.'
      };
    }
    
    // IMPORTANT: Only delete rows that are within the specified range
    // This prevents accidentally deleting rows outside the range (like A1)
    const minRowIndex = checkRange.rowIndex;
    const maxRowIndex = checkRange.rowIndex + checkRange.rowCount - 1;
    
    // Filter out any row indices that are outside the specified range
    const rowsToDelete = emptyRows.filter(rowIdx => rowIdx >= minRowIndex && rowIdx <= maxRowIndex);
    
    console.log('Rows to delete (filtered):', rowsToDelete);
    
    // Delete rows from bottom to top to avoid index shifting
    rowsToDelete.sort((a, b) => b - a); // Sort in descending order
    
    for (const rowIndex of rowsToDelete) {
      console.log('Deleting row:', rowIndex + 1); // Display 1-based for user
      // Get the entire row using a fixed large column count to avoid getUsedRange() issues
      const rowToDelete = worksheet.getRangeByIndexes(rowIndex, 0, 1, 1000);
      rowToDelete.delete(Excel.DeleteShiftDirection.up);
      await context.sync();
    }
    
    return {
      success: true,
      message: `${rowsToDelete.length}개의 빈 행이 제거되었습니다.`
    };
    
  } catch (error) {
    console.error('Error in executeCompress:', error);
    return {
      success: false,
      error: `빈 행 제거 중 오류 발생: ${error.message}`
    };
  }
}

async function executeFilter(context, params) {
  const worksheet = context.workbook.worksheets.getActiveWorksheet();
  const range = params.range ? worksheet.getRange(params.range) : worksheet.getUsedRange();
  
  // Apply autofilter
  range.worksheet.autoFilter.apply(range);
  
  await context.sync();
  
  return {
    success: true,
    message: '필터가 적용되었습니다.'
  };
}

async function executeInsert(context, params) {
  const worksheet = context.workbook.worksheets.getActiveWorksheet();
  const type = params.type || 'row';
  const position = params.position || 1;
  const count = params.count || 1;
  
  if (type === 'row') {
    const range = worksheet.getRangeByIndexes(position - 1, 0, count, 1);
    range.insert(Excel.InsertShiftDirection.down);
  } else {
    const range = worksheet.getRangeByIndexes(0, position - 1, 1, count);
    range.insert(Excel.InsertShiftDirection.right);
  }
  
  await context.sync();
  
  return {
    success: true,
    message: `${count}개의 ${type === 'row' ? '행' : '열'}이 삽입되었습니다.`
  };
}

async function executeDelete(context, params) {
  const worksheet = context.workbook.worksheets.getActiveWorksheet();
  const type = params.type || 'row';
  const position = params.position || 1;
  const count = params.count || 1;
  
  if (type === 'row') {
    const range = worksheet.getRangeByIndexes(position - 1, 0, count, 1);
    range.delete(Excel.DeleteShiftDirection.up);
  } else {
    const range = worksheet.getRangeByIndexes(0, position - 1, 1, count);
    range.delete(Excel.DeleteShiftDirection.left);
  }
  
  await context.sync();
  
  return {
    success: true,
    message: `${count}개의 ${type === 'row' ? '행' : '열'}이 삭제되었습니다.`
  };
}

async function executeFormula(context, params) {
  const worksheet = context.workbook.worksheets.getActiveWorksheet();
  const range = params.range ? worksheet.getRange(params.range) : context.workbook.getSelectedRange();
  
  if (!params.formula) {
    return { success: false, error: '수식을 지정해주세요.' };
  }
  
  range.formulas = [[params.formula]];
  await context.sync();
  
  return {
    success: true,
    message: '수식이 적용되었습니다.'
  };
}

